<template>
  <v-container>
     <v-row class="text-center" justify="center">
      <v-col cols = "6">
       <h2>音高识别</h2>
       <v-btn small color = "primary" class = "mt-2" @click="recordAction"> 
          <span v-if="isRecording">结束录制</span>
          <span v-else>开始录制</span>
        </v-btn>
      </v-col>
      <v-col cols = "6" >
        <!-- <v-avatar tile color ="primary" size = "6em" class = "white--text">
          <div id="pitch_name" style="fontSize:3em;position:absolute">{{pitch_name}}</div>
          <div id="degree_name" style="fontSize:1em;position:absolute;marginLeft:4em;marginBottom:4em">{{degree}}</div>
          <div id="cents_name" style="fontSize:1em;position:absolute;marginTop:4em">{{cents}}</div>
        </v-avatar> -->
        <v-card class="mx-auto white--text" max-width="6em" max-height="6em" color = "primary" >
          <v-card-text class="white--text" style="padding:0">
            <div id="pitch_name"  class="display-1 "> {{pitch_name}} </div>
            <div id="degree_name"> {{degree}}</div>
            <div id="cents_name" >
                {{cents}} cents
            </div>
          </v-card-text>
        </v-card>
        
        
      </v-col>
    </v-row>
    
    <v-layout row wrap class="mx-2 my-1">
      <v-flex xs12>
        <h4 class ="">音量能量:{{db.toFixed(3)}} 采样长度:{{micGain}} </h4>
      </v-flex>
      <v-flex xs12>
        <h5 class="mt-3">如果发现音量能量总是为0或者过大，请调整麦克风音量:</h5>
         <v-slider min="0.0" max="5.0" label="mic音量" v-model="micGain" step="0.1"></v-slider>
      </v-flex>
      <v-flex xs12>
        <h4 class="mt-3">实时音频</h4>
        <v-divider></v-divider>
      </v-flex>
     
      <v-flex xs12>
        <canvas id="audio_canvas" style="border: 1px solid black; width:100%; height:7em"></canvas>
      </v-flex>
    </v-layout>
    <v-layout row wrap class="mt-1 mx-2">
      <v-flex xs12>
        <h4 class="mt-3">音高曲线</h4>
        <v-divider></v-divider>
      </v-flex>
      <v-flex xs12>
        <canvas id="pitch_canvas" style="border: 1px solid black; width:100%; height:28em"></canvas>
      </v-flex>
    </v-layout>
    <v-layout column wrap v-if="recordings.length > 0">
      <h4 class="mt-3">已保存录音</h4>
      <div v-for="(recording,idx) in recordings" :key="recording.ts">
        <v-card>
          <v-card-title primary-title>
            <v-layout column wrap>
              <div style="fontSize:0.8em">
                录音-{{ idx + 1 }}
              </div>
              <div class="ml-3">
                <div>
                  <audio :src="recording.blobUrl" :type="recording.mimeType" controls="true"/>
                </div>
                <div  style="fontSize:0.5em">
                   大小: {{recording.size}}, 类型: {{recording.mimeType}}
                </div>
              </div>
            </v-layout>
          </v-card-title>
        </v-card>
        <v-divider v-if="idx !== (recordings.length-1)"/>
      </div>
    </v-layout>
  </v-container>
</template>

<script>

import RecorderService from '../plugins/RecorderService'
import $ from "jquery"
import PD from "../plugins/pitch_detect"

// import utils from '../plugins/Utils'

const SMOOTHING = 0.9
const FFT_SIZE = 2048

export default {
  name: 'PitchDetect',
  data: () => ({
    echoCancel: false,
    isRecording: false,
    mimeTypes: [],
    recordings: [],
    micGain: 1,
    cleanup: true,
    fs:44100,
    dynamicCompress: false,
    pitch_name:'A',
    cents:'0',
    degree:'4',
    maxFreq: 90,
    pitchMaxX:300,
    pitchSeq:[],
    pitchColor:[],
    curPos:0,
    isCalc: true,
    db:0,
    ratio:1,
    sampleLen:0
  }),
  methods: {
    recordAction() {
      if(this.isRecording) {
        this.stopRecording()
      }
      else {
        this.startRecording()
      }
    },
    startRecording () {
      this.recorder.config.stopTracksAndCloseCtxWhenFinished = this.cleanup
      this.recorder.config.createDynamicsCompressorNode = this.dynamicCompress
      this.recorder.config.enableEchoCancellation = this.echoCancel
      this.recorder.config.micGain = this.micGain
      // this.isCalc = true
      this.recorder.startRecording()
        .then(() => {
          this.isRecording = true
          this.analyser = this.recorder.analyserNode
          this.audioCtx = this.recorder.audioCtx
          this.fs = this.analyser.context.sampleRate
          this.analyser.minDecibels = -140
          this.analyser.maxDecibels = 0
          this.freqs = new Uint8Array(this.analyser.frequencyBinCount)
          this.times = new Uint8Array(this.analyser.frequencyBinCount)
          this.sampleLen = this.analyser.frequencyBinCount
          this.ratio = 1
          // console.log(this.ratio)

          this.audio_canvas = $("#audio_canvas").get(0)
          let drawContext = this.audio_canvas.getContext('2d')
          drawContext.scale(this.ratio, this.ratio)
          // this.audio_canvas.width = this.audio_canvas.width
          // this.audio_canvas.height = this.audio_canvas.height
          this.audio_canvasWidth = this.audio_canvas.width / this.ratio
          this.audio_canvasHeight = this.audio_canvas.height / this.ratio

          this.pitch_canvas = $("#pitch_canvas").get(0)
          drawContext = this.pitch_canvas.getContext('2d')
          drawContext.scale(this.ratio, this.ratio)
          // this.pitch_canvas.width = this.pitch_canvas.width
          // this.pitch_canvas.height = this.pitch_canvas.height
          this.pitch_canvasWidth = this.pitch_canvas.width / this.ratio
          this.pitch_canvasHeight = this.pitch_canvas.height / this.ratio

          window.requestAnimationFrame(this.draw.bind(this))
        })
        .catch((error) => {
          console.error('Recording Failed: ' + error)
          alert('Recording Failed: ' + error.message)
        })
    },
    stopRecording () {
      this.recorder.stopRecording()
      this.isRecording = false
    },
    onNewRecording (evt) {
      this.recordings.push(evt.detail.recording)
    },
    draw() {
      this.analyser.smoothingTimeConstant = SMOOTHING
      this.analyser.fftSize = FFT_SIZE
      
      this.analyser.getByteFrequencyData(this.freqs)
      this.analyser.getByteTimeDomainData(this.times)
      let t_value = new Array()
      let f_value = new Array()
      for (let i = 0; i < this.analyser.frequencyBinCount;++i) {
        t_value.push((this.times[i] / 256 - 0.5) * this.micGain)
        f_value.push((this.freqs[i] / 256 - 0.5) * this.micGain)
      }
      let ans_fre = PD.get_frequency(t_value, this.fs)
      let ans_pitch = PD.get_pitch(ans_fre[0])
      // console.log(this.freqs)
      // console.log(this.times)
      // draw audio canvas
      let canvas = this.audio_canvas
      let drawContext = canvas.getContext('2d')
      let cWidth = this.audio_canvasWidth
      let cHeight = this.audio_canvasHeight
      drawContext.clearRect(0, 0, cWidth, cHeight)
     
      // Draw the frequency domain chart.
      for (let i = 0; i < this.analyser.frequencyBinCount; i++) {
        let value = this.freqs[i] / 256 
        let height = cHeight * value
        let offset = cHeight - height - 1
        let barWidth = cWidth / this.analyser.frequencyBinCount
        let hue = i / this.analyser.frequencyBinCount * 360
        drawContext.fillStyle = 'hsl(' + hue + ', 100%, 50%)'
        drawContext.fillRect(i * barWidth, offset, barWidth, height)
        // console.log(hue)
      }
      // Draw the time domain chart.
      drawContext.fillStyle = 'gray'
      for (let i = 0; i < this.analyser.frequencyBinCount; i++) {
        let value = this.times[i] / 256 + (this.times[i] / 256 - 0.5) * (this.micGain-1)
        let height = cHeight * value
        let offset = cHeight - height - 1
        let barWidth = cWidth / this.analyser.frequencyBinCount
        drawContext.fillRect(i * barWidth, offset, 1, 2)
      }

      // draw pitch canvas
      canvas = this.pitch_canvas
      drawContext = canvas.getContext('2d')
      cWidth = this.pitch_canvasWidth
      cHeight = this.pitch_canvasHeight
      drawContext.clearRect(0, 0, cWidth, cHeight)
      // update the pitch sequence
      for(let i = this.pitchSeq.length - 1;i > 0;--i) {
        this.pitchSeq[i] = this.pitchSeq[i - 1]
        this.pitchColor[i] = this.pitchColor[i - 1]
      }
      this.pitchSeq[0] = ans_fre[0]
      this.db = ans_fre[1]
      // console.log(ans_fre[1])
      // if (ans_fre[1] > 0.03)this.pitchColor[0] = 'red'
      // else this.pitchColor[0] = 'gray'
      // Draw the pitch_contour.
      
      for (let i = 0; i < this.pitchMaxX - 1; i += 2) {
        drawContext.fillStyle = 'rgba(255,0,0,1)'
        let value =  (12 * (Math.log(this.pitchSeq[i] / 440) / Math.log(2)) + 40) / this.maxFreq
        let diff = Math.abs(Math.log((this.pitchSeq[i] + 1.1)/ 440) / Math.log(2) - Math.log((this.pitchSeq[i + 1] + 1.1) / 440) / Math.log(2)) * 12
        let height = cHeight * value
        let offset = cHeight - height - 1
        let barWidth = cWidth / this.pitchMaxX * 2
        
        // drawContext.fillStyle = this.pitchColor[i]
        if (diff > 1) {
          // console.log(diff)
          drawContext.fillStyle = 'rgba(255,0,0,0.2)'
        }
        else {
          if (this.isCalc) {
            this.pitch_name = ans_pitch[0]
            this.degree = ans_pitch[1]
            this.cents = ans_pitch[2]
          }
        }
        drawContext.fillRect(Math.floor(i/2) * barWidth, offset, barWidth, 1)
        // console.log(hue)
      }
      // this.isCalc = !this.isCalc
      // console.log(ans_fre)
      // console.log(ans_pitch)
      if (this.isRecording) {
        window.requestAnimationFrame(this.draw.bind(this))
      }

    },
  },
  created() {
    this.recorder = new RecorderService()
    this.recorder.config.createAnalyserNode = true
    this.recorder.em.addEventListener("recording", (evt) => this.onNewRecording(evt))
    this.curPos = 0
    for (let i = 0; i < this.pitchMaxX;++i) {
      this.pitchSeq[i] = 0.0
      this.pitchColor[i] = 'rgba(255, 0, 0,1)'
    }
  }
}
</script>
